body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  background: rgb(15, 75, 110);
 
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d4e4ef', endColorstr='#86aecc', GradientType=0 );

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1{
  color: #ccc;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
}
label {
  font-size: 18px;
}
.container {
  margin-right: 2vw;
  margin-left: 2vw;
}

/***************************************
      Footer
****************************************/
.fixed_footer {
  width: 100%;
  height: 150px;
  background: #111;
  left: 0;
  bottom: 0;
  color: #696969;
}
.content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: center;
}
/***************************************
      Title
****************************************/
.container-logo {
  height: 250px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
}
.logo {
  height: 250px;
  margin: 5px;
  padding: 15px;
  border: 6px solid #ccc;
  border-radius: 15px;
}

.logo-title {
  font-size: 50px;
}
/***************************************
      ListProducts
****************************************/
.list-products {
  margin-top: 100px;
  margin-bottom: 15px;
}
.items {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-auto-rows: 1fr;
}
.product-item {
  padding: 15px;
  border: 1px solid #777;
}

.product-item img {
  width: 100%;
  height: 60%;
}

.product-cost {
  font-size: 25px;
  font-weight: bold;
  color: #d14f4f;
}
.oferta {
  font-size: 12px;
  color: #48ff00;
}
/***************************************
      PRimary button
****************************************/
.product-btn {
  margin-left: 5rem;
}
.button-primary {
  font-size: 16px;
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.button-primary:before {
  content: '';
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.button-primary:active {
  color: #000;
}

.button-primary:active:after {
  background: transparent;
}

.button-primary:hover:before {
  opacity: 1;
}

.button-primary:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
/***************************************
      Product
****************************************/
.multi-color {
  color: #f39c12;
  margin-bottom: -55px;
}
.show-Info {
  margin-top: 5vw;
  margin-bottom: 5vw;
  justify-content: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 15px;
  grid-auto-rows: auto;
}
.show-Info img {
  margin: auto;
  width: 70%; /*70%*/
  height: 95%; /*95%*/
}

.show-Info div {
  margin-top: 10px;
  margin-bottom: 10px;
}
.error-msm {
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #f04c4c;
  padding: 20px 25px;
  border-radius: 20px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.13);
}
select {
  font-size: 15px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #2c3e50;
  background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.select {
  position: relative;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #2c3e50;
  overflow: hidden;
  border-radius: 0.25em;
}
select {
  flex: 1;
  padding: 0 0.5em;
  color: #fff;
  cursor: pointer;
}
/* Arrow */
.select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #34495e;
  cursor: pointer;
  pointer-events: none;
  -webkit-transition: 0.25s all ease;
  -o-transition: 0.25s all ease;
  transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
  color: #f39c12;
}
/***************************************
      Radio button
****************************************/

.list {
  -webkit-box-flex: 0;
  flex: 0 0 20rem;
}

.list__item {
  position: relative;
  list-style-type: none;
}
.list__item:hover > .label {
  color: #2c3e50;
}
.list__item:hover > .label::before {
  border: 0.5rem solid #2c3e50;
  margin-right: 2rem;
}

.radio-btn {
  position: absolute;
  opacity: 0;
  visibility: hidden;
}
.radio-btn:checked + .label {
  color: #2c3e50;
}
.radio-btn:checked + .label::before {
  margin-right: 2rem;
  border: 0.5rem solid #2c3e50;
  background: #fff;
}

.label {
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  padding: 0.75rem 0;
  color: #fff;
  font-size: 1.25rem;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

.label::before {
  display: inline-block;
  content: '';
  height: 1.125rem;
  width: 1.125rem;
  margin-right: 0.625rem;
  border: 0.5rem solid #fff;
  border-radius: 50%;
  -webkit-transition: all 0.25s linear;
  transition: all 0.25s linear;
}
/***************************************
      Ready
****************************************/
.ready-grid{
  margin-top: 100px !important;
  margin-bottom: 35px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 25px;
}
.ready-grid img{
  height: 70%;
  width: 350px;
}

.centerX
{
  margin-top: 50px;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
}

.card {
  background-color: white;
  width: 380px;
  min-height: 404.5px;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  padding: 20px;
  margin: 5px;
  box-shadow: -20px -20px 0px 0px #fb968b;
  border-radius: 10px;
  -webkit-animation-name: shadow-show;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 1.5s;
  /* Safari 4.0 - 8.0 */
  animation-name: shadow-show;
  animation-duration: 1.5s;
  -webkit-transition-timing-function: cubic-bezier(0.795, 0, 0.165, 1);
  transition-timing-function: cubic-bezier(0.795, 0, 0.165, 1);
  /* custom */
}
.card h1, .card h2, .card h3, .card h4, .card h5 {
  margin: 0px;
  padding: 0px 0px 15px 0px;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 30px;
  color: #282828;
  text-align: center;
}
.card hr {
  display: block;
  border: none;
  height: 3px;
  background-color: salmon;
  margin: 0px;
  -webkit-animation-name: line-show;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 0.3s;
  /* Safari 4.0 - 8.0 */
  animation-name: line-show;
  animation-duration: 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.795, 0, 0.165, 1);
  transition-timing-function: cubic-bezier(0.795, 0, 0.165, 1);
  /* custom */
}
.card p {
  margin: 15px 0px 0px 0px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 100;
  letter-spacing: -0.25px;
  line-height: 1.25;
  font-size: 16px;
  word-break: break-all;
  word-wrap: pre-wrap;
  color: #282828;
  -webkit-animation-name: p-show;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 1.5s;
  /* Safari 4.0 - 8.0 */
  animation-name: p-show;
  animation-duration: 1.5s;
}
.card button {
  border: none;
  background-color: salmon;
  width: 50%;
  margin: 10px auto;
  padding: 10px 30px;
  color: white;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 25px;
  font-weight: bold;
  text-transform: uppercase;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes line-show {
  from {
    margin: 0px 100px;
  }
  to {
    margin: 0px;
  }
}
/* Standard syntax */
@keyframes line-show {
  from {
    margin: 0px 100px;
  }
  to {
    margin: 0px;
  }
}
/* Safari 4.0 - 8.0 */
@-webkit-keyframes p-show {
  from {
    color: white;
  }
  to {
    color: #282828;
  }
}
/* Standard syntax */
@keyframes p-show {
  from {
    color: white;
  }
  to {
    color: #282828;
  }
}
/* Safari 4.0 - 8.0 */
@-webkit-keyframes shadow-show {
  from {
    box-shadow: 0px 0px 0px 0px #e0e0e0;
  }
  to {
    box-shadow: -20px -20px 0px 0px #fb968b;
  }
}
/* Standard syntax */
@keyframes shadow-show {
  from {
    box-shadow: 0px 0px 0px 0px #e0e0e0;
  }
  to {
    box-shadow: -20px -20px 0px 0px #fb968b;
  }
}


/***************************************
      Render
****************************************/
@media screen and (max-width: 600px) {
  h1 {
    text-align: center;
  }
  .container {
    margin-left: 15px;
    margin-right: 15px;
  }
  .container-logo {
    display: block;
  }
  .logo {
    height: 180px;
  }
  .logo-title {
    text-align: center;
  }
  .show-Info {
    margin-top: 25vw;
    margin-left: 15px;
    grid-template-columns: 1fr;
  }
  .show-Info img {
    width: 350px;
    height: 350px;
  }
  .items {
    grid-template-columns: 350px;
  }
  .ready-grid{
    display: block;
    padding-top: 70px;
  }
  .ready-grid img{
     height: 70%;
    width: 290px;
  }
  .card{
    margin-top: 80px !important;
    width: 290px;
  }
}
@media screen and (max-width: 999px) {
  .show-Info img {
    width: 350px;
    height: 400px;
  }
  .ready-grid{
    margin-left: 10px;
  }
  .card{
    margin-top: 80px !important;
  }
}
